import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core';
import { withTranslation } from 'react-i18next';
import Radio from '@material-ui/core/Radio';
import LinearProgress from '@material-ui/core/LinearProgress';
import { borderStyle } from '../../Theme';
import './PollOption.css';

const styles = theme => ({
    progressRoot: {
        backgroundColor: 'transparent',
        margin: '2px 0 0 38px',
        position:'absolute',
        left: 0,
        right: 0,
        bottom: 0
    },
    radioRoot: {
        marginLeft: -10,
        marginRight: -12,
        position: 'fixed'
    },
    radioBar: {
        transition: 'transform .2s linear'
    },
    ...borderStyle(theme)
});

class PollOption extends React.Component {

    getTitleString = (count, t = key => key) => {
        if (!count) return t('NoVotes').toLowerCase();
        if (count === 1) return '1 vote';

        return count + 'votes';
    };

    handleClick = event => {
        const { canBeSelected } = this.props;
        if (!canBeSelected) return;

        event.stopPropagation();
    };

    render() {
        const { classes, option, onChange, canBeSelected, maxVoterCount, t } = this.props;
        if (!option) return null;

        const { text, voter_count, vote_percentage, is_chosen, is_being_chosen } = option;

        let value = 1.5;
        if (voter_count){
            value = voter_count / maxVoterCount * 100;
        }

        return (
            <div className='poll-option' onClick={this.handleClick}>
                { canBeSelected
                    ? (
                        <>
                            {/*<Radio classes={{ root: classes.radioRoot }} color='primary' onChange={onChange}/>*/}
                            {/*{text}*/}
                            <div className='poll-option-wrapper'>
                                <div className='poll-option-text-wrapper'>
                                    <Radio classes={{ root: classes.radioRoot }} color='primary' onChange={onChange}/>
                                    <div className='poll-option-text'>
                                        {text}
                                    </div>
                                </div>
                            </div>
                            <div className={classNames('poll-option-bottom-border', classes.borderColor)}/>
                            <LinearProgress classes={{ root: classes.progressRoot, bar: classes.radioBar }} color='primary' variant='determinate' value={0}/>
                        </>
                    ) : (
                        <>
                            <div className='poll-option-wrapper'>
                                <div className='poll-option-text-wrapper' title={this.getTitleString(voter_count, t)}>
                                    <div className={classNames('poll-option-percentage', {'subtitle': !is_chosen})}>
                                        {vote_percentage + '%'}
                                    </div>
                                    <div className='poll-option-text'>
                                        {text}
                                    </div>
                                </div>
                            </div>
                            <div className='poll-option-bottom-border'/>
                            <LinearProgress classes={{ root: classes.progressRoot, bar: classes.radioBar }} color='primary' variant='determinate' value={Math.max(1.5, value)}/>
                        </>
                    )
                }
            </div>
        );
    }
}

PollOption.propTypes = {
    option: PropTypes.object.isRequired,
    canBeSelected: PropTypes.bool,
    maxVoterCount: PropTypes.number
};

const enhance = compose(
    withStyles(styles, { withTheme: true }),
    withTranslation()
);

export default enhance(PollOption);